import { Component, OnInit, Output, EventEmitter, ɵConsole } from '@angular/core';
import { Router, Event, NavigationStart, NavigationEnd, NavigationError, ActivatedRoute } from '@angular/router';
import { Location } from '@angular/common';
@Component({
    selector: 'app-categories',
    templateUrl: './categories.component.html',
    styleUrls: ['./categories.component.scss']
})
export class CategoriesComponent implements OnInit {
    public pathOfCategories:string = "";
    
    constructor(private location: Location, private router:ActivatedRoute, public currentRouter:Router) {
            currentRouter.events.subscribe( (event: Event) => {
                if (event instanceof NavigationStart) {
                    // Show loading indicator
                }
                if (event instanceof NavigationEnd) {
                    // Hide loading indicator
                    this.changePath.emit(event.url.split("#")[event.url.split("#").length-1].replace(/%20/g, ' '))
                }
                if (event instanceof NavigationError) {
                    // Hide loading indicator
                    console.log("error")
                }
            });
        
    }
    
    @Output() changePath = new EventEmitter();
    ngOnInit() {
    }
    isActiveCategory(routerParam:any){
        return routerParam == this.router.snapshot.fragment;
    }
}
