import { Component, OnInit, Input } from '@angular/core';
import * as firebase from "firebase/app";
import { AuthServiceService } from "../../../services/auth-service.service";

@Component({
    selector: 'app-register-form',
    templateUrl: './register-form.component.html',
    styleUrls: ['./register-form.component.scss']
})
export class RegisterFormComponent implements OnInit {
    public showRegFormPassNotSame: boolean = false;
    public showRegFormPassLargeThenSix: boolean = true;
    public regForm:CreateUser = {
        firstName: '',
        lastName: '',
        emailTrue: '',
        password: '',
        confirmPass: '',
    }
    constructor(private authServ:AuthServiceService) { }
    @Input('isShowBoolean') isShowBoolean: boolean;
    ngOnInit() {
    }

    createUser({ value }: { value: CreateUser }) {
        this.authServ.SignUp(value.emailTrue, value.password)
    }
}

interface CreateUser {
    firstName: string;
    lastName: string;
    emailTrue: string;
    password: string;
    confirmPass: string;
}
